import './layout.css';

import { useQuery } from '@apollo/react-hooks';
import gql from 'graphql-tag';
import React from 'react';

import LoginView from '../containers/loginContainer';
import LoadingView from '../containers/loadingContainer';
import Footer from './footer';
import Header from './header';

export interface Props {
  children: any;
  loadingStart: Function,
  loadingEnd: Function,
  getUserStations: Function,
}

// localStorage.setItem('sewage:token', "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJTZXdhZ2UiLCJpZCI6IjE1NzUyODEyNDcyMTU2NzY1NjgiLCJleHAiOjE1NzU4OTM3NDh9._OYzI7AF-JTr62bJ2IveBBwUwCo3MTkdsEu-QAnjzLU");
// localStorage.clear();

const VERIFY_TOKEN = gql`
  query VERIFY_TOKEN($token: String!) {
    verifyToken(token: $token) {
      token
      errorMsg
    }
  }
`;

const Layout = ({
  children,
  loadingStart,
  loadingEnd,
  getUserStations,
}: Props): React.ReactElement => {
  let token;
  if (typeof localStorage !== 'undefined') {
    token = localStorage.getItem('sewage:token');
  }
  const { data, loading, error } = useQuery(
    VERIFY_TOKEN,
    { variables: { token } });

  if (loading) {
    loadingStart();
  } else {
    loadingEnd();
  }

  if (data &&
      data.verifyToken &&
      data.verifyToken.errorMsg === 'ok') {
      if (typeof localStorage !== 'undefined') {
        localStorage.setItem('sewage:token', data.verifyToken.token);
      }
      getUserStations();
  } else {
      children = (<LoginView />);
  }

  return (
    <>
      <Header />
      <LoadingView />
        {!loading &&
          (<main className="main">{children}</main>)
        }
      <Footer />
    </>
  );
};

export default Layout;
